

import program from 'commander';
import { install as installSourceMapSupport } from 'source-map-support';
import { resolve as resolvePath } from 'path';
import { fullStack as formatStack } from 'verror';
import * as Bluebird from 'bluebird';
import { Cli } from '../lib/cli';
import { StripAnsiTransform } from '../lib/strip-ansi';
import * as R from 'ramda';

Bluebird.config({ longStackTraces: true });

installSourceMapSupport();

program
  .usage('[options] [-- <cucumber-args ...>]')
  .option('-p, --port <port>', 'The port to bind', 8000)
  .option('-h, --host <hostname>', 'The host to bind', 'localhost')
  .option('-c, --command <cmd>', 'The command to run to start the server', null)
  .option('-s, --score', 'Print the score')
  .option('-D, --no-debug', 'Disable debug output')
  .option('-C, --no-colors', 'Disable color output')
  .parse(process.argv);

if (!program.command) {
  console.log('--command is required');
  process.exit(1);
}

global.program = program;

const cli = new Cli({
  cwd: process.cwd(),
  options: {
    featurePaths: [ resolvePath(__dirname, '../../features') ],
    stepPaths: [ resolvePath(__dirname, '../steps') ],
    ...R.pick([
      'colors',
      'debug',
      'port',
      'host',
      'command',
    ], program),
  },
  cucumberArgs: program.args,
  stdout: getOutStream(program.colors),
});

process.on('unhandledRejection', reason => {
  console.log('UNHANDLED REJECTION', reason);
});

process
  .on('SIGHUP', () => cli.gracefullyExit())
  .on('SIGINT', () => cli.gracefullyExit())
  .on('SIGQUIT', () => cli.gracefullyExit())
  .on('SIGTERM', () => cli.forceExit())
  .on('exit', () => cli.forceExit());

cli
  .precheck()
  // .then(({ ok, message }) => {
  //   if (ok)
  //     return cli.run();

  //   console.log(message);
  //   return null;
  // })
  .catch(err => {
    console.error(formatStack(err));
    process.exit(1);
  });


function getOutStream(enableColors) {
  if (enableColors) return process.stdout;

  const out = new StripAnsiTransform();
  out.pipe(process.stdout);

  return out;
}
